import React from 'react'
import './ProCategories'
import './ProOverview'
import ProOverview from './ProOverview'
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { CardActionArea } from '@mui/material';
import ProCategories from './ProCategories';
import Footer from './Footer'
const Home = () => {
    return (
        <div className='home'>
            <div className='row slider'>
                <div className='col'>
                    <div id="carouselExampleControls" class="carousel slide carousel-fade" data-bs-ride="carousel">
                        <div class="carousel-inner">
                            <div class="carousel-item active">
                                <img src="https://fashionbi-assets.s3.amazonaws.com/uploads/publication/carousel/file/96/slider_2.jpg" class="d-block w-100" alt="..." />

                            </div>
                            {/* <div class="carousel-item">
                        <img src="https://www.portotheme.com/magento/porto/media/wysiwyg/porto/homepage/slider/04/slide1.jpg" class="d-block w-100" alt="..." />
                    </div> */}
                            <div class="carousel-item">
                                <img src="http://www.norwegianfashion.net/slide_images/1486377558_slider.jpg" class="d-block w-100" alt="..." />
                            </div>
                        </div>
                        <button class="carousel-control-prev " type="button" data-bs-target="#carouselExampleControls" data-bs-slide="prev">
                            <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                            <span class="visually-hidden">Previous</span>
                        </button>
                        <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="next">
                            <span class="carousel-control-next-icon" aria-hidden="true"></span>
                            <span class="visually-hidden">Next</span>
                        </button>
                    </div>
                </div>
            </div>
            <ProCategories />
            <ProOverview />
            <Footer />
            
        </div>
    )
}

export default Home
