import React from 'react'
import { Typography } from '@mui/material'
import { Button } from '@mui/material'
import DeleteIcon from '@mui/icons-material/Delete';
import FavoriteIcon from '@mui/icons-material/Favorite';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import ProductCard from './ProductCard';
import { products } from './ProOverview'
import Footer from './Footer';
const ProductDisplay = () => {


    return (
        <div>


            <div className='row mt-5 ms-4' style={{ maxWidth: "80%" }}>
                <div className='col-md-4'>
                    <div id="carouselExampleIndicators" className="carousel slide" data-bs-ride="carousel">
                        <div className="carousel-indicators">
                            <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="0" className="active" aria-current="true" aria-label="Slide 1"></button>
                            <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="1" aria-label="Slide 2"></button>
                            <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="2" aria-label="Slide 3"></button>
                        </div>
                        <div className="carousel-inner">
                            <div className="carousel-item active">
                                <img src="https://preview.colorlib.com/theme/cozastore/images/xproduct-02.jpg.pagespeed.ic._mIojWDfEX.webp" className="d-block w-100" alt="..." />

                            </div>
                            <div className="carousel-item">
                                <img src="https://preview.colorlib.com/theme/cozastore/images/xproduct-01.jpg.pagespeed.ic.6WHvZRJRuO.webp" className="d-block w-100" alt="..." />
                            </div>
                            <div className="carousel-item">
                                <img src="https://preview.colorlib.com/theme/cozastore/images/xproduct-02.jpg.pagespeed.ic._mIojWDfEX.webp" className="d-block w-100" alt="..." />
                            </div>
                        </div>
                        <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide="prev">
                            <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                            <span className="visually-hidden">Previous</span>
                        </button>
                        <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide="next">
                            <span className="carousel-control-next-icon" aria-hidden="true"></span>
                            <span className="visually-hidden">Next</span>
                        </button>
                    </div>
                </div>
                
                <div className='col-md-8'>
                    <div className='card border border-0'>
                        <div className='card-header border border-0' style={{ background: "white" }}>
                            <Typography className='mx-auto' variant="h4" component="div" style={{ fontFamily: "Roboto" }} gutterbottom>Formal White Shirt</Typography>
                            <Typography className='mx-auto mt-1' variant="subtitle1" component="div" style={{ fontFamily: "Roboto", fontWeight: "bold" }} gutterbottom>14.00$</Typography>

                        </div>
                        <div className='card-body border border-0'>
                            <Typography className='mx-auto' variant="button" component="div" style={{ fontFamily: "Roboto" }} gutterbottom>Description:</Typography>
                            <Typography className='mx-auto' variant="body1" component="div" style={{ fontFamily: "Roboto" }} gutterbottom>
                                Aenean sit amet gravida nisi. Nam fermentum est felis, quis feugiat nunc fringilla sit amet. Ut in blandit ipsum. Quisque luctus dui at ante aliquet, in hendrerit lectus interdum. Morbi elementum sapien rhoncus pretium maximus. Nulla lectus enim, cursus et elementum sed, sodales vitae eros. Ut ex quam, porta consequat interdum in, faucibus eu velit.
                            </Typography>
                            {/* <Button variant="contained" color="danger" startIcon={<FavoriteIcon />}>
                                Delete
                            </Button> */}


                        </div>
                        <div className='card-footer border border-0' style={{ background: "white" }}>
                            <Button style={{ marginRight: "30px", marginTop: "20px" }} variant="outlined" color="success" startIcon={<ShoppingCartIcon />}>Add to Cart</Button>
                            <Button style={{ marginTop: "20px" }} variant="contained" color="error" startIcon={<FavoriteIcon />}>Add to Wishlist</Button>
                        </div>
                    </div>
                </div>

            </div>

            <Typography variant="h4" style={{ fontWeight: "bold", textAlign: "center", marginTop: "3%" }} component="div" gutterBottom>
                Related Products
            </Typography>
            <div className='row '>


                {products.slice(0, 3).map((item) => {
                    return (
                        <div className='col-md-4 col-sm-6 mx-4 my-5  ' style={{ maxWidth: "400px", maxHeight: "400px" }}>
                            <ProductCard
                                imageurl={item.imageurl}
                                title={item.title}
                                price={item.price}></ProductCard>
                        </div>
                    )
                })}

            </div>

            <Footer />
        </div>

    )
}

export default ProductDisplay
