import React from 'react'
import Counter from './Counter'
import { Typography } from '@mui/material'
import { TextField } from '@mui/material'
import { Button } from '@mui/material'
import SendIcon from '@mui/icons-material/Send';
import { purple, red, grey } from '@mui/material/colors';
import Footer from './Footer'
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
var SvgLines = require('react-mt-svg-lines').default;

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: 'none',
    boxShadow: 24,
    p: 4,
};

const Cart = () => {
    const greyColor = grey[900];
    const [open, setOpen] = React.useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    return (
        <div className='cart'>
            <div className='row ms-3 mb-4 mt-5'>
                <div className='col-7 justify-content-center align-items-center'>
                    <div className='card' >
                        <div className='card-header' style={{ background: "white" }}>
                            <div className='row'>
                                <div className='col-4'>
                                    <Typography variant="subtitle1" gutterBottom component="div">
                                        Product
                                    </Typography>
                                </div>
                                <div className='col-2'>
                                    <Typography variant="subtitle1" gutterBottom component="div">
                                        Price
                                    </Typography>
                                </div>
                                <div className='col-3'>
                                    <Typography variant="subtitle1" gutterBottom component="div">
                                        Quantity
                                    </Typography>
                                </div>
                                <div className='col-2'>
                                    <Typography variant="subtitle1" gutterBottom component="div">
                                        Total
                                    </Typography>
                                </div>
                            </div>
                        </div>
                        <div className='card-body '>
                            <div className='row mb-4 border-bottom-1'>
                                <div className='col-3 border-1' style={{ maxWidth: "50px", maxHeight: "50px" }}>
                                    <img width="100%" src="https://preview.colorlib.com/theme/cozastore/images/xproduct-03.jpg.pagespeed.ic.eOHs429Gtb.webp" />

                                </div>
                                <div className='col-3'>
                                    <Typography variant="subtitle2" gutterBottom component="div">
                                        Formal Blue check shirt
                                    </Typography>
                                </div>
                                <div className='col-2 ms-2'>
                                    <Typography variant="subtitle2" gutterBottom component="div">
                                        14.00$
                                    </Typography>
                                </div>
                                <div className='col-3'>
                                    <Counter></Counter>
                                </div>
                                <div className='col-2 ms-2'>
                                    <Typography variant="subtitle2" gutterBottom component="div">
                                        14.00$
                                    </Typography>
                                </div>
                            </div>
                            <div className='row border-bottom-1'>
                                <div className='col-3 border-1' style={{ maxWidth: "50px", maxHeight: "50px" }}>
                                    <img width="100%" src="https://preview.colorlib.com/theme/cozastore/images/xproduct-03.jpg.pagespeed.ic.eOHs429Gtb.webp" />

                                </div>
                                <div className='col-3'>
                                    <Typography variant="subtitle2" gutterBottom component="div">
                                        Formal Blue check shirt
                                    </Typography>
                                </div>
                                <div className='col-2 ms-2'>
                                    <Typography variant="subtitle2" gutterBottom component="div">
                                        14.00$
                                    </Typography>
                                </div>
                                <div className='col-3'>
                                    <Counter></Counter>
                                </div>
                                <div className='col-2 ms-2'>
                                    <Typography variant="subtitle2" gutterBottom component="div">
                                        14.00$
                                    </Typography>
                                </div>
                            </div>
                        </div>
                        <div class="card-footer my-4" style={{ background: "white" }}>
                            <div className='row'>
                                <div className='col-5'>
                                    <TextField fullWidth id="standard-basic" label="Coupon code" variant="standard" />
                                </div>
                                <div className='col-3 ms-5 mt-3'>
                                    <Button variant="outlined" color="success">Apply Coupon</Button>
                                </div>
                                <div className='col-3 mt-3'>
                                    <Button variant="contained" color="success" >
                                        UPDATE CART
                                    </Button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className='col-4 ms-5'>
                    <div className='card'>
                        <div className='card-header border-bottom-0 mx-auto' style={{ background: "white" }}>
                            <Typography className='mx-auto' variant="h6" component="div" style={{ fontFamily: "Roboto" }} gutterbottom>ORDER DETAILS</Typography>

                        </div>
                        <div className='card-body'>
                            <div className='row'>

                                <div className='col-8 mx-auto' >
                                    <TextField className="mt-2 mx-auto" fullWidth id="standard-basic" label="House No." variant="standard" />

                                </div>
                                <div className='col-8 mx-auto' >
                                    <TextField className="mt-3 mx-auto" fullWidth id="standard-basic" label="City" variant="standard" />

                                </div>
                                <div className='col-8 mx-auto' >
                                    <TextField className="mt-3 mx-auto" fullWidth id="standard-basic" label="Pin Code" variant="standard" />

                                </div>
                                <div className='col-8 mt-4 mx-auto'>
                                    <input class="form-control" list="datalistOptions" id="exampleDataList" placeholder="Payment Mode" />
                                    <datalist id="datalistOptions">
                                        <option value="UPI" />
                                        <option value="PayTM" />
                                        <option value="GooglePay" />
                                        <option value="COD" />
                                        <option value="PhonePe" />
                                    </datalist>
                                </div>
                            </div>

                        </div>
                        <div className='card-footer' style={{ background: "white" }}>
                            <div className='row justify-content-center'>
                                <div className='col-8 '>
                                    <Typography className='text-align-center' variant="subtitle2" component="div" style={{ fontFamily: "Roboto" }} gutterbottom>SubTotal:</Typography>

                                </div>
                                <div className='col-4'>
                                    35.00$
                                </div>
                            </div>
                            <div className='row'>
                                <div className='col-8 '>
                                    <Typography className='mx-auto' variant="subtitle2" component="div" style={{ fontFamily: "Roboto" }} gutterbottom>Add. Charges:</Typography>

                                </div>
                                <div className='col-4'>
                                    35.00$
                                </div>
                            </div>
                            <div className='row'>
                                <div className='col-8 '>
                                    <Typography className='mx-auto' variant="subtitle2" component="div" style={{ fontFamily: "Roboto" }} gutterbottom>Grand Total:</Typography>

                                </div>
                                <div className='col-4'>
                                    35.00$
                                </div>

                                <Button style={{ maxWidth: "120px", marginLeft: "130px", marginRight: "auto" }}
                                    onClick={handleOpen} variant="contained" color="success">
                                    BUY NOW
                                </Button>

                                <Modal
                                    open={open}
                                    onClose={handleClose}
                                    aria-labelledby="modal-modal-title"
                                    aria-describedby="modal-modal-description"
                                >
                                    <Box sx={style}>
                                        <SvgLines animate={true} duration={500}>
                                            <svg viewBox="0 0 100 100">
                                                <path stroke="green" strokeWidth="10" fill="none" d="M20.8,51c0,0,20.8,18.2,21.5,18.2c0.6,0,33.3-38.5,33.3-38.5" />
                                            </svg>
                                        </SvgLines>
                                        <Typography className="mx-auto" id="modal-modal-title" variant="button" display="block" component="div">
                                            Your order has been placed!!
                                        </Typography>

                                    </Box>
                                </Modal>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
            <Footer></Footer>
        </div>
    )
}

export default Cart
