import React,{useState} from 'react'

const Counter = () => {
    const [quantity, setQuantity] = useState(0);

    return (
        <div className='row ' style={{maxWidth:"10rem",maxHeight:".5%"}}>
            <div className='col-3'>
                <button className='btn btn-dark border-0'  onClick={()=>{setQuantity(quantity > 0? quantity-1 :0);}}>-</button>
            </div>
            <div className='col-3 justify-content-center border-dark'>
                <h5 className='text-align-center ms-2 mt-1'>{quantity}</h5>
            </div>
            <div className='col-3'>
            <button className='btn btn-dark border-0' onClick={()=>{setQuantity(quantity+1);}}>+</button>
            </div>
        </div>
    )
}

export default Counter
