import React from 'react'
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { CardActionArea } from '@mui/material';

const ProCategories = () => {
    return (

        <div className='pro-categories' styles={{justifyContent:"center"}} className="mx-auto">

            <Typography variant="h3" component="div" style={{marginTop:"2%",textAlign:"center"}} gutterBottom>
                Categories
            </Typography>
            <div className='row my-5 justify-content-around'>
                <div className='col-md-4'>

                    <Card sx={{ maxWidth: 345, maxHeight: 500 }} className='mx-auto'>
                        <CardActionArea>
                            <CardMedia
                                component="img"
                                height="200"
                                image="https://preview.colorlib.com/theme/cozastore/images/xbanner-01.jpg.pagespeed.ic.Uj5FE94mgU.webp"
                                alt="green iguana"
                            />
                            <CardContent>
                                <Typography gutterBottom variant="h5" component="div">
                                    Women
                                </Typography>
                                <Typography variant="body2" color="text.secondary">
                                    Latest srping 2021 collection
                                </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>

                </div>
                <div className='col-md-4'>
                    <Card sx={{ maxWidth: 345 }} className='mx-auto'>
                        <CardActionArea>
                            <CardMedia
                                component="img"
                                height="200"
                                image="https://preview.colorlib.com/theme/cozastore/images/xbanner-02.jpg.pagespeed.ic.MQuZq6F18q.webp"
                                alt="green iguana"
                            />
                            <CardContent>
                                <Typography gutterBottom variant="h5" component="div">
                                    Women
                                </Typography>
                                <Typography variant="body2" color="#333">
                                    Latest spring 2021 collection
                                </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </div>
                <div className='col-md-4'>
                    <Card sx={{ maxWidth: 345 }} className='mx-auto'>
                        <CardActionArea>
                            <CardMedia
                                component="img"
                                height="200"
                                image="https://preview.colorlib.com/theme/cozastore/images/xbanner-03.jpg.pagespeed.ic.1rqLomOaMb.webp"
                                alt="green iguana"
                            />
                            <CardContent>
                                <Typography gutterBottom variant="h5" component="div">
                                    Women
                                </Typography>
                                <Typography variant="body2" color="text.secondary">
                                    Latest Spring 2021 collection
                                </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </div>
            </div>

        </div>

    )
}

export default ProCategories
