import React, { useState } from 'react'
import Typography from '@mui/material/Typography';
import { styled } from '@mui/material/styles';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardMedia from '@mui/material/CardMedia';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import Collapse from '@mui/material/Collapse';
import Avatar from '@mui/material/Avatar';
import IconButton from '@mui/material/IconButton';
import { red } from '@mui/material/colors';
import FavoriteIcon from '@mui/icons-material/Favorite';
import ShareIcon from '@mui/icons-material/Share';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import { Link } from "react-router-dom"
import { Button } from '@mui/material';

const ProductCard = (props) => {
    const [color, setColor] = useState('black');
    return (
        <Card style={{border:"none",boxShadow:"none",maxHeight:"500px"}}>
            <CardMedia
                component="img"
                height="345"
                image={props.imageurl}
                alt=""
            />
            <div className="row">
                <div className='col-9'>
                    <CardContent >
                        <Link
                            style={{ fontFamily: "Roboto", textDecoration: "none", color: "#333", fontWeight: "bold" }}
                            to="/product_display">
                            {props.title}
                        </Link>
                        <Typography variant="body2" color="subtitle1">
                            {props.price}
                        </Typography>
                    </CardContent>
                </div>
                <div className='col-3 mt-3 justify-content-end align-items-end'>
                    <IconButton aria-label="add to favorites"
                        style={{ color: color, marginTop: "0" }}
                        onClick={() => { color == 'black' ? setColor("red") : setColor('black') }}>
                        <FavoriteIcon />
                    </IconButton>
                </div>
            </div>
        </Card>
    )
}

export default ProductCard
